import React, {Component} from 'react';
import Slider from 'react-slick';

 class TestimonialSlider extends Component {
    render() {
      const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        arrows: false,
        autoplay: true,
        pauseOnHover:true,
        autoplaySpeed: 10000,
        slidesToShow: 1,
        slidesToScroll: 1
      };
      return (
        <div>
          <Slider {...settings} className="testimonial_slider">
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/Sofia.jpg')} alt="Sofia De Bellis"/>
                    </div>
                    <h6>Sofia De Bellis</h6>
                    <span>Marketing Subcommittee Member</span>
                    <p>Keith is a super welcoming and friendly person who is always around down to chill or go karaoke! 
                      As a first year not in his subcom I can attest to his approachable nature and his amiable personality which 
                      I have experienced when working with him on various CSESoc projects and when hanging out at society bonding events :DD</p>
                </div>
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/Ryan.jpg')} alt="Ryan Shi"/>
                    </div>
                    <h6>Ryan Shi</h6>
                    <span>Competitions Director, 3rd Year Sturep</span>
                    <p>
                      Having organised a few events together, Keith is always unafraid to take the lead when things are uncertain 
                      and just as bold to take the blame for the team when things get sour, showing traits of a true leader. 
                      Keith's air of friendliness draws all around to his cause and I've always admired his ability to find ways to relate with everyone.
                      I'm confident that Keith will shine through brightly as a future executive for CSESoc, and can't wait to see what he'll bring to the table next year!</p>
                </div>
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/Xenocide.jpg')} alt="Jack Li"/>
                    </div>
                    <h6>Jack Li</h6>
                    <span>2nd Year Sturep</span>
                    <p>Keith is a friendly and approachable person who is dedicated to improving the experience of CSE students. 
                      The content he has created with CSESoc Media showcases his resourcefulness and his ability to get the most out of others :)</p>
                </div>
          </Slider>
        </div>
      );
    }
  }
  export default TestimonialSlider;