import React, {Component} from 'react';
import ItemGrid from './ItemGrid';
import Sectiontitle from '../../component/Banner/Sectiontitle';
import Image from 'react-bootstrap/Image';
import { Container, Col, Row } from 'react-bootstrap';
import '../../assets/css/portfolio.css';

class portfolio extends Component {
    render() {
      var {pClass}= this.props;
      return(
        <section className={`portfolio_area ${pClass}`} id="portfolio">
            <div className="container">
                <Sectiontitle Title="Contributions"/>
                <p>Over the past year, I've had the privilege of being a Media director to oversee videos, podcasts and articles produced by CSESoc. 
                  This year in the media portfolio we placed emphasis on producing content with focus on inclusivity and diversity. With my wonderful
                  co-directors Amanda and Caleb as well as our amazing subcom, here is some of the awesome content our portfolio produced this year.</p>
                  <br></br>
                <ItemGrid/>
                <br></br>
                <Container>
                  <Row id='otherCont'>
                  <Col>
                    <Image fluid src={require('../../image/collage1.png')}></Image>
                  </Col>
                  <Col>
                    <div>
                      <p style={{textAlign:"left"}}>
                        Besides my role as a director, I've been highly involved with CSESoc and CSE itself during my
                        two years of university. This includes:
                      </p>
                      <ul>
                        <li>2022 Welcome Week Stall Volunteer</li>
                        <li>2022 First Year Camp Leader</li>
                        <li>COMP1531 Tutor</li>
                        <li>2022 Studex Peer Mentor</li>
                        <li>2022 O-Week Stall Volunteer</li>
                        <li>2022 CompClub Summer Workshop Mentor</li>
                        <li>2021 Media Subcommittee</li>
                        <li>2021 First Year Campee</li>
                      </ul>
                    </div>
                  </Col>
                  </Row>
                </Container>
            </div>
        </section>
      )
    }
  }

  export default portfolio;
